import React from 'react'
import { Container } from 'reactstrap';
import LocationNavigator from '../components/directories-navigator.component';
import LocationViewer from '../components/directory-viewer.component';

const LocationsPage = () => {
    return (
        <Container className="pt-5" >
            <LocationNavigator />
            <LocationViewer />
        </Container>
    )
}

export default LocationsPage