import React from 'react'
import { Container, Row, Col } from 'reactstrap';
import DirectoryTile from './directory-tile.component'
import { connect } from 'react-redux';

const DirectoryViewer = (props) => {
    let children = props.currentContent;
    return (
        <Container className="pt-1" >
            <Row>
                {
                    children.map((child, i) => {
                        return (
                            <Col className="my-1" md="4" key={i}>
                                <DirectoryTile data={child} />
                            </Col>
                        )
                    })
                }
            </Row>
        </Container>
    )
}
const mapStateToProps = state => {
    return {
        currentContent: state.directories.currentContent
    }
}

export default connect(mapStateToProps, null)(DirectoryViewer);