import { ADD_TO_BREAD_CRUMB_PATH, SET_CURRENT_CONTENT } from "../actions/types";

let initState = {
    currentPath: [{
        id: 1,
        name: 'My Pc'
    }],
}
const BreadCrumbPath = (state = initState, action) => {
    switch (action.type) {
        case ADD_TO_BREAD_CRUMB_PATH:
            let bc = state.currentPath;
            bc[action.payload.level - 1] = {
                id: action.payload.id,
                name: action.payload.name
            };
            return {
                currentPath: [...bc],
            }
        case SET_CURRENT_CONTENT:
            let index = action.payload.index;
            let bcTwo = state.currentPath.filter((p, i) => i < index + 1);
            return {
                ...state,
                currentPath: [...bcTwo]
            }
        default:
            return state
    }
}
export default BreadCrumbPath;