import React from 'react';
import { Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { connect } from 'react-redux';
import { setCurrentContent } from '../store/actions';

const DirectoryNavigator = (props) => {
    let path = props.currentPath;
    const onClickHandler = (path, index) => {
        props.setCurrentContent(path, index)
    }
    return (
        <div>
            <Breadcrumb tag="nav" listTag="div">
                {
                    path.map((p, i) => {
                        return (
                            <BreadcrumbItem onClick={() => {
                                onClickHandler(p,i)
                            }} key={i} tag="a" href="#">{p.name}</BreadcrumbItem>
                        )
                    })
                }
            </Breadcrumb>
        </div>
    );
};
const mapStateToProps = state => {
    return {
        currentPath: state.BreadCrumbPath.currentPath
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setCurrentContent : (path, index) => dispatch(setCurrentContent(path,index))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(DirectoryNavigator);