import { ADD_TO_BREAD_CRUMB_PATH, SET_CURRENT_CONTENT } from "./types"

export const addToCrumbPath = data => {
    return {
        type: ADD_TO_BREAD_CRUMB_PATH,
        payload: data
    }
}

export const setCurrentContent = (path, index) => {
    return {
        type: SET_CURRENT_CONTENT,
        payload: { path, index }
    }
}