import React, { useState, useEffect } from 'react'
import { Card, CardBody, CardTitle, CardSubtitle, CardImg, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { addToCrumbPath } from '../store/actions';
import { connect } from 'react-redux';

const DirectoryTile = ({ data, addToCrumbPath }) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);
    const onClickHandler = (data) => {

        if (data.type === "file") {
            toggle()
        } else {
            addToCrumbPath(data);
        }

    }
    useEffect(() => { }, [modal])
    return (
        <React.Fragment>
            <Card style={{ cursor: "pointer" }} onClick={() => {
                onClickHandler(data)
            }}>
                <CardBody>
                    {data.type === "drive" && <CardImg top width="100%" height={250} src="https://findicons.com/files/icons/684/vistoon/256/hdd.png" alt="Card image cap" />}
                    {data.type === "dir" && <CardImg top width="100%" height={250} src="https://png.pngtree.com/element_our/png_detail/20181229/vector-folder-icon-png_302656.jpg" alt="Card image cap" />}
                    {data.type === "file" && <CardImg top width="100%" height={250} src={data.srcLink} alt="Card image cap" />}
                    <CardTitle>
                        <h6>{data.name}</h6>
                    </CardTitle>
                    <CardSubtitle>Created On : {data.createdOn}</CardSubtitle>
                </CardBody>
            </Card>
            <Modal isOpen={modal} toggle={toggle} >
                <ModalHeader toggle={toggle} className="text-center">{data.name}</ModalHeader>
                <ModalBody>
                   <img className="w-100" src={data.srcLink} alt=""/>     
                </ModalBody>
                <ModalFooter>

                </ModalFooter>
            </Modal>
        </React.Fragment>


    )
}

const mapDispatchToProps = dispatch => {
    return {
        addToCrumbPath: (data) => dispatch(addToCrumbPath(data))
    }
}

export default connect(null, mapDispatchToProps)(DirectoryTile);