let allChilds = [];
const getAllChilds = (data, path) => {

    if (data.name === path.name) {
        allChilds = data;
    } else if (data != null) {
        data.children.forEach((value, key) => {
            if (value.name === path.name && value.id === path.id) {
                return allChilds = value;
            } else if (value.children.length > 0) {
                getAllChilds(value, path)
            }
        });
    }
    return allChilds;
}
