const directories = (state = initState, action) => {
    state.currentContent = state.root.children
    switch (action.type) {
        case ADD_TO_BREAD_CRUMB_PATH:
            let level = action.payload.level;
            let selectedContentId;
            let currentContent = [];

            currentContent = action.payload.children;

            return {
                ...state,
                selectedContentId,
                currentContent
            }
        case SET_CURRENT_CONTENT:
            let level2 = action.payload.index + 1;
            let pathName = action.payload.path;
            let currentContent2 = [];

            currentContent2 = getAllChilds(state.root, pathName).children;

            return {
                ...state,
                currentContent: currentContent2
            }
        default:
            return state
    }
}
export default directories